function dungeonDescriptionGenerator(){var descriptions=['A short boulder in a misty grove marks the entrance to this dungeon. Beyond the boulder lies a modest, worn room. It\'s covered in moss, dead vermin and rat droppings.\nYour torch allows you to see a broken statue part of a fountain, frayed and eaten by time itself.\n\nFurther ahead are two paths, but the right is a dead end. Its twisted trail leads passed broken and pillaged tombs and soon you enter a dank area. There are several braziers scattered around, somehow they\'re still burning, or burning again. What happened in this place?\n\nYou proceed onwards, deeper into the dungeon\'s shadows. You pass a few more passages, some are dead ends, others lead to who knows where, or what. You eventually make it to what is likely the final room. A wide metal door blocks your path. Ominous symbols are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. light\'s coming through the gap below the door.','A small murky cave in a foggy mountain range marks the entrance to this dungeon. Beyond the murky cave lies a scanty, dark room. It\'s covered in roots, broken pottery and dead vermin.\nYour torch allows you to see broken cages and torture devices, decayed and wiped out by time itself.\n\nFurther ahead are two paths, you take the right. Its twisted trail leads onwards and soon you enter a worn area. Several stacks of gunpowder barrels are stacked against a wall. A skeleton holding a torch lies before it. What happened in this place?\n\nYou slowly move onwards, deeper into the dungeon\'s shadows. You pass various different rooms and countless passages, most of which probably lead to other depths of this dungeon. You eventually make it to what is likely the final room. A massive wooden door blocks your path. Countless odd symbols are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you think you can hear a whisper coming from behind the door.','A modest murky cave in a somber snowland marks the entrance to this dungeon. Beyond the murky cave lies a small, putrid room. It\'s covered in small bones, cobwebs and rubble.\nYour torch allows you to see drawings and symbols on the walls, forgotten and dismantled by time itself.\n\nFurther ahead are two paths, but the right is a dead end. Its twisted trail leads passed broken and pillaged tombs and soon you enter a dark area. There\'s a huge skeleton in the center, along with dozens of human skeletons. What happened in this place?\n\nYou slowly move onwards, deeper into the dungeon\'s secrets. You pass a few more rooms and passages, most of which are far too ominous looking to try out. You eventually make it to what is likely the final room. An enormous metal door blocks your path. Countless runes are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you\'re pretty sure you\'re being watched.','A large fallen tower in a shadowy cliff side marks the entrance to this dungeon. Beyond the fallen tower lies a narrow, humid room. It\'s covered in ash, broken stone and dead vermin.\nYour torch allows you to see an overgrown underground garden, frayed and maimed by time itself.\n\nFurther ahead are three paths, you take the middle. Its twisted trail leads passed countless rooms and soon you enter a foggy area. There\'s a huge skeleton in the center, along with dozens of human skeletons. What happened in this place?\n\nYou proceed onwards, deeper into the dungeon\'s mysteries. You pass a few more passages, some look awfully familiar, others stranger everything else. You eventually make it to what is likely the final room. A big metal door blocks your path. Countless runes are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you hear a loud bang in the distance from which you came.','A wide waterfall in a shadowy cliff side marks the entrance to this dungeon. Beyond the waterfall lies a grand, damp room. It\'s covered in crawling insects, roots and cobwebs.\nYour torch allows you to see remnants of a small camp, destroyed and consumed by time itself.\n\nFurther ahead are two paths, but the left is a dead end. Its twisted trail leads passed countless other pathways and soon you enter a dark area. There\'s a demolished door with a sign that says "don\'t open". What happened in this place?\n\nYou proceed onwards, deeper into the dungeon\'s shadows. You pass various different rooms and countless passages, it\'s one big labyrinth of twists and turns. You eventually make it to what is likely the final room. An ominous wooden door blocks your path. Dire warning messages are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. was that a growl coming from behind the door?','A short murky cave in a sinister marsh marks the entrance to this dungeon. Beyond the murky cave lies a narrow, shady room. It\'s covered in roots, broken pottery and roots.\nYour torch allows you to see weapons racks and locked crates, aged and drained by time itself.\n\nFurther ahead are two paths, you take the left. Its twisted trail leads passed several empty rooms and soon you enter a grimy area. Countless traps, swinging axes and other devices move all around. They\'re either still active, or just activated. What happened in this place?\n\nYou proceed onwards, deeper into the dungeon\'s shadows. You pass many different passages, some of them have collapsed, others seem to go on forever. You eventually make it to what is likely the final room. A mysterious wooden door blocks your path. Dried blood splatters are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you\'re pretty sure you\'re being watched.','A grand granite door in a sinister wasteland marks the entrance to this dungeon. Beyond the granite door lies a massive, shady room. It\'s covered in rubble, crawling insects and roots.\nYour torch allows you to see a broken statue part of a fountain, deteriorated and wrecked by time itself.\n\nFurther ahead are three paths, you take the middle. Its twisted trail leads passed pillaged rooms and soon you enter a timeworn area. There\'s a pile of skeletons in the center, all burned and black. What happened in this place?\n\nYou slowly move onwards, deeper into the dungeon\'s secret passages. You pass a few more passages, it\'s one big labyrinth of twists and turns. You eventually make it to what is likely the final room. A wide metal door blocks your path. Various odd symbols are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. light\'s coming through the gap below the door.','A massive pair of granite doors in a dark forest marks the entrance to this dungeon. Beyond the pair of granite doors lies a massive, grimy room. It\'s covered in moss, ash and remains.\nYour torch allows you to see a broken tomb, busted and dismantled by time itself.\n\nFurther ahead are three paths, you take the right. Its twisted trail leads passed long lost rooms and tombs and soon you enter a grimy area. Several stacks of gunpowder barrels are stacked against a wall. A skeleton holding a torch lies before it. What happened in this place?\n\nYou slowly move onwards, deeper into the dungeon\'s secret passages. You pass many rooms and passages, each with their own twists, turns and destinations. You eventually make it to what is likely the final room. A massive granite door blocks your path. Messages in strange languages are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. what was that sound?','A narrow broken temple in a eerie cliff side marks the entrance to this dungeon. Beyond the broken temple lies a narrow, deteriorated room. It\'s covered in dead vermin, cobwebs and rubble.\nYour torch allows you to see what seems like some form of a sacrificial chamber, battered and dismantled by time itself.\n\nFurther ahead are two paths, you take the left. Its twisted trail leads passed countless other pathways and soon you enter a ghastly area. Several cages hold skeletal remains of various animals. Next to the cages are odd machines. What happened in this place?\n\nYou carefully continue onwards, deeper into the dungeon\'s expanse. You pass various different rooms and countless passages, each with their own twists, turns and destinations. You eventually make it to what is likely the final room. A mysterious wooden door blocks your path. Countless runes are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you think you can hear a whisper coming from behind the door.','A modest waterfall in a somber marsh marks the entrance to this dungeon. Beyond the waterfall lies a modest, worn room. It\'s covered in ash, roots and crawling insects.\nYour torch allows you to see empty chests and broken statues, worn and butchered by time itself.\n\nFurther ahead are two paths, but the right is a dead end. Its twisted trail leads passed pillaged rooms and soon you enter a worn area. There\'s a round stone in the center, around it are a dozen skeletons in a circle. What happened in this place?\n\nYou carefully continue onwards, deeper into the dungeon\'s secrets. You pass dozens of similar rooms and passages, most of which probably lead to other depths of this dungeon. You eventually make it to what is likely the final room. A thick metal door blocks your path. Countless runes are all over it, somehow untouched by time and the elements. You step closer to inspect it and.. wait.. you hear a loud bang in the distance from which you came.'];return descriptions[Object(__WEBPACK_IMPORTED_MODULE_0__Helper_js__["f" /* getRandomIntInclusive */])(0,descriptions.length-1)];}

